<template>
    <div class="container-fluid wrapper">
    <h2>Forum Dreamer-online</h2>
    <div>
        <h3 class="group-header"> </h3>
        <div class="group-topics">
            <ul>
                <li class="themegroup" @click="routerTest('news')">  </li>
                <li class="themegroup" @click="routerTest('information')"> </li>
                <li class="themegroup" @click="routerTest('promotions')">  </li>
                <li class="themegroup" @click="routerTest('qas')">  </li>
            </ul>
        </div>

        <h3 class="group-header"></h3>
        <div class="group-topics">
            <ul>
                <li class="themegroup" @click="routerTest('tavern')"></li>
                <li class="themegroup" @click="routerTest('clans')">  </li>
                <li class="themegroup" @click="routerTest('marketplace')"></li>
            </ul>
        </div>

        <h3 class="group-header"> </h3>
        <div class="group-topics">
            <ul>
                <li class="themegroup" @click="routerTest('support')"> </li>
                <li class="themegroup" @click="routerTest('bugs')">  </li>
            </ul>
        </div>

    </div>
    </div>
</template>

<script>
    export default {
        name: 'app',
        data() {
            return {

            }
        },
        methods: {
            routerTest(themegroup) {
                this.$router.push({name: 'themegroup', params:{Tid:themegroup}});
            }
        }
    }
</script>
   

<style>
    .themegroup {
        cursor: pointer;
        list-style-type: none;
    }
    h2, h3 {
        text-shadow: 0px 3px 3px white;
    }
    a {
        text-decoration: none;
        color: white;
        text-shadow: 0px 3px 3px black;
    }
</style>


